import React, { FC } from 'react';
import Section from './section';

interface Project {
  name: string;
  background: string;
  logo: string;
  description: string;
  keywords: string[];
}

interface Props {
  data: Project[];
}

const Card: FC<Project> = ({ name, background, logo, description, keywords }) => (
  <div className="flex sm:block rounded shadow overflow-hidden transition duration-200 ease-in-out hover:shadow-xl hover:scale-105 mb-8 sm:mb-0">
    <div className="h-64 flex justify-center items-center" style={{ background }}>
      <img src={logo} alt="project-logo" className="mx-auto w-3/6 rounded-full sm:rounded-none" />
    </div>
    <div className="px-6 pt-4 pb-2">
      <div className="font-bold text-gray-900 sm:text-xl xl:text-base mb-2">{name}</div>
      <div className="text-gray-700 text-base mb-4">{description}</div>
      {keywords && keywords.length ? (
        <div className="flex flex-wrap justify-start -mx-2">
          {keywords.map((keyword) => (
            <div
              key={keyword}
              className="inline-block bg-gray-200 rounded-full px-3 py-1 text-xs font-medium text-gray-800 m-1"
            >
              {keyword}
            </div>
          ))}
        </div>
      ) : null}
    </div>
  </div>
);

const Portfolio: FC<Props> = ({ data }) => (
  <Section title="My Portfolio">
    <div className="sm:grid sm:grid-cols-2 xl:grid-cols-3 col-gap-4 row-gap-8">
      {data.map((project) => (
        <Card key={project.name} {...project} />
      ))}
    </div>
  </Section>
);

export default Portfolio;
